export default {
    props:['zulipopupHead'],
    data(){
        return {
            //zulipopupHead2 : this.zulipopupHead
        }
    },
    methods: {
        setMsg() {
            this.$emit('rentnode',0)
        }
    },
    computed:{
        modeType(){
            let vm = this;
            let type = 0;
            if(vm.zulipopupHead == '随租随还'){
                type = 1;
            }else if(vm.zulipopupHead == '固定租期'){
                type = 2;
            }else if(vm.zulipopupHead == '租完即送'){
                type = 3;
            }else if(vm.zulipopupHead == '短期租赁'){
                type = 4;
            }else{
                type = 5;
            }
            return type;
        }
    }
}



