export default{
    props:['title'],
    data(){
        return{
            phone:''
        }
    },
    methods:{
        telChange(){
            let vm=this;
            vm.$emit('phoneSay',vm.phone);
        }
    },
    watch:{
        phone(val,oldval){
            let vm=this;
            let newLen=val.length,oldLen=oldval.length;
            let reg=/^[1]/;
            if(!reg.test(vm.phone)){
                vm.phone='';
            }
            if(newLen>oldLen){
                if(this.phone.length==3||this.phone.length==8){
                    this.phone=this.phone+' ';
                }
            }
            
        }
    }
}



