
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default{
components:{
XButton,Group,Flexbox,FlexboxItem
},
methods: {

goregister(){
let vm = this;
vm.$router.push("/register?url="+vm.url);
},
gologin(){
let vm = this;
vm.$router.push("/login?url="+vm.url);
}
},
computed:{
url:function () {
let url = (this.$route.query.url)?(this.$route.query.url):'';
return url;
}
},
created(){
this.$store.dispatch('isLoading',false);
}
}



